const $siteList = $(".siteList");
const $lastLi = $siteList.find("li.last");
const x = localStorage.getItem("x"); //ÂæóÂà∞ÁºìÂ≠ò
//localStorage.getItem() :    ÂæóÂà∞ÁºìÂ≠ò
const xObject = JSON.parse(x);
//JSON.parse():ÊñπÊ≥ïÁî®Êù•Ëß£ÊûêJSONÂ≠óÁ¨¶‰∏≤,ÊûÑÈÄ†Áî±Â≠óÁ¨¶‰∏≤ÊèèËø∞ÁöÑJavaScriptÂÄºÊàñÂØπË±°.
const hashMap = xObject || [
  //ÁºìÂ≠ò
  { url: "https://www.bilibili.com" },
  { url: "https://www.acfun.cn" }
];
//search input ÈòªÊ≠¢ÂÜíÊ≥°document

$(".globalSearch").on("input", function(e) {
  e.stopPropagation();
});
//Event.stopPropagation()ÔºöÊñπÊ≥ïÈòªÊ≠¢ÊçïËé∑ÂíåÂÜíÊ≥°Èò∂ÊÆµ‰∏≠ÂΩìÂâç‰∫ã‰ª∂ÁöÑËøõ‰∏ÄÊ≠•‰º†Êí≠

//Ê∏≤ÊüìÂø´Êç∑ÊñπÂºè
const render = () => {
  $siteList.find("li:not(.last)").remove();
  hashMap.forEach((node, index) => {
    //foreach():ÊñπÊ≥ïÂØπÊï∞ÁªÑÁöÑÊØè‰∏™ÂÖÉÁ¥†ÊâßË°å‰∏ÄÊ¨°Êèê‰æõÁöÑÂáΩÊï∞
    const $li = $(`<li>
      <div class="site">
        <div class="logo">
          <img src="${node.url.slice(
            0,
            node.url.indexOf(".com") === -1
              ? node.url.length
              : node.url.indexOf(".com") + 4
          )}/favicon.ico" alt="" />
        </div>
        <div class="link">${
          node.url.slice(0, 12) === "https://www."
            ? node.url.slice(
                12,
                node.url.indexOf(".com") === -1
                  ? node.url.length
                  : node.url.indexOf(".com") + 4
              )
            : node.url.slice(
                8,
                node.url.indexOf(".com") === -1
                  ? node.url.length
                  : node.url.indexOf(".com") + 4
              )
        }</div>
        <div class="close">
          <svg class="icon">x
            <use xlink:href="#icon-close"></use>
          </svg>
        </div>
      </div>
    </li>`).insertBefore($lastLi);
    $li.on("click", () => {
      window.open(node.url); //windowÊé•Âè£ÁöÑopen()ÊñπÊ≥ï,ÊòØÁî®ÊåáÂÆöÁöÑÂêçÁß∞Â∞ÜÊåáÂÆöÁöÑËµÑÊ∫êÂä†ËΩΩÂà∞ÊµèËßàÂô®‰∏ä‰∏ãÊñá(Á™óÂè£window,ÂÜÖÂµåÊ°ÜÊû∂iframeÊàñËÄÖtabÊ†áÁ≠æ)„ÄÇÂ¶ÇÊûúÊ≤°ÊúâÊåáÂÆöÔºåÂàôÊñ∞Á™óÂè£
    });

    $li.on("click", ".close", e => {
      e.stopPropagation();
      hashMap.splice(index, 1);
      render();
    });
  });
  if (hashMap.length <= 9) {
    $(".last").css("display", "block");
  } else {
    $(".last").css("display", "none");
  }
};
//ËøêË°åÁºìÂ≠òÊï∞ÊçÆ
render();
//Ê∑ªÂä†Âø´Êç∑ÊñπÂºè
$(".addButton").on("click", () => {
  let url = window.prompt("ËæìÂÖ•xx.com or www.xx.com ÁΩëÂùÄ;");
  //window.prompt() :ÊòæÁ§∫‰∏Ä‰∏™ÂØπËØùÊ°Ü,ÂØπËØùÊ°Ü‰∏≠ÂåÖÂê´‰∏ÄÊù°ÊñáÂ≠ó‰ø°ÊÅØ,Áî®Êù•ÊèêÁ§∫Áî®Êà∑ËæìÂÖ•ÊñáÂ≠ó
  //Èò≤Ê≠¢Âá∫Áé∞‰∏§‰∏™
  if (url !== "") {
    if (url.indexOf("https://") !== 0) {
      url = "https://" + url;
      let = result = 1;
      hashMap.forEach(Node => {
        Node.url === url ? (result = 0) : 1;
      });
      if (result) {
        hashMap.push({ url: url });
        render();
      }
    }
  }
  if (hashMap.length >= 10) {
    $(".last").css("display", "none");
  }
});
//‰ΩøÁî®ÁÉ≠ÈîÆÂø´ÈÄüÊâìÂºÄÁΩëÁ´ôÂø´Êç∑ÊñπÂºè
$(document).on("keypress", e => {
  const { key } = e;
  if (key >= 1 && key <= 9) {
    window.open(hashMap[key - 1].url);
  } else if (key === "0") {
    window.open(hashMap[hashMap.length - 1].url);
  }
});

//Á™óÂè£ÂÖ≥Èó≠Êó∂ÁºìÂ≠òÊï∞ÊçÆ
window.onbeforeunload = () => {
  const string = JSON.stringify(hashMap);
  //JSON.stringify() ÊñπÊ≥ïÂ∞Ü‰∏Ä‰∏™JavaScriptÂÄº(ÂØπË±°ÊàñËÄÖÊï∞ÁªÑ)ËΩ¨Êç¢‰∏∫‰∏Ä‰∏™JSONÂ≠óÁ¨¶‰∏≤,Â¶ÇÊûúÊåáÂÆö‰∫ÜreplacerÊòØ‰∏Ä‰∏™ÂáΩÊï∞
  localStorage.setItem("x", string);
  //setItem() ‰Ωú‰∏∫storageÊé•Âè£ÁöÑÊñπÊ≥ï,Êé•Êî∂‰∏Ä‰∏™ÈîÆÂêçÂíåÂÄº‰Ωú‰∏∫ÂèÇÊï∞,Â∞Ü‰ºöÊääÈîÆÂêçÊ∑ªÂä†Âà∞Â≠òÂÇ®‰∏≠,Â¶ÇÊûúÈîÆÂêçÂ∑≤Â≠òÂú®,ÂàôÊõ¥Êñ∞ÂÖ∂ÂØπÂ∫îÁöÑÂÄº
};

